import React, { Component } from "react";
import MybookDataService from "../services/mybook.service";

export default class Mybook extends Component {
  constructor(props) {
    super(props);
    this.onChangeTitle = this.onChangeTitle.bind(this);
    this.onChangeDescription = this.onChangeDescription.bind(this);
    this.getMybook = this.getMybook.bind(this);
    this.updatePublished = this.updatePublished.bind(this);
    this.updateMybook = this.updateMybook.bind(this);
    this.deleteMybook = this.deleteMybook.bind(this);

    this.state = {
      currentMybook: {
        id: null,
        title: "",
        description: "",
        published: false
      },
      message: ""
    };
  }

  componentDidMount() {
    this.getMybook(this.props.match.params.id);
  }

  onChangeTitle(e) {
    const title = e.target.value;

    this.setState(function(prevState) {
      return {
        currentMybook: {
          ...prevState.currentMybook,
          title: title
        }
      };
    });
  }

  onChangeDescription(e) {
    const description = e.target.value;
    
    this.setState(prevState => ({
      currentMybook: {
        ...prevState.currentMybook,
        description: description
      }
    }));
  }

  getMybook(id) {
    MybookDataService.get(id)
      .then(response => {
        this.setState({
          currentMybook: response.data
        });
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  updatePublished(status) {
    var data = {
      id: this.state.currentMybook.id,
      title: this.state.currentMybook.title,
      description: this.state.currentMybook.description,
      published: status
    };

    MybookDataService.update(this.state.currentMybook.id, data)
      .then(response => {
        this.setState(prevState => ({
          currentMybook: {
            ...prevState.currentMybook,
            published: status
          }
        }));
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  updateMybook() {
    MybookDataService.update(
      this.state.currentMybook.id,
      this.state.currentMybook
    )
      .then(response => {
        console.log(response.data);
        this.setState({
          message: "The mybook was updated successfully!"
        });
      })
      .catch(e => {
        console.log(e);
      });
  }

  deleteMybook() {    
    MybookDataService.delete(this.state.currentMybook.id)
      .then(response => {
        console.log(response.data);
        this.props.history.push('/mybooks')
      })
      .catch(e => {
        console.log(e);
      });
  }

  render() {
    // ...
    const { currentMybook } = this.state;

    return (
      <div>
        {currentMybook ? (
          <div className="edit-form">
            <h4>Mybook</h4>
            <form>
              <div className="form-group">
                <label htmlFor="title">Title</label>
                <input
                  type="text"
                  className="form-control"
                  id="title"
                  value={currentMybook.title}
                  onChange={this.onChangeTitle}
                />
              </div>
              <div className="form-group">
                <label htmlFor="description">Description</label>
                <input
                  type="text"
                  className="form-control"
                  id="description"
                  value={currentMybook.description}
                  onChange={this.onChangeDescription}
                />
              </div>

              <div className="form-group">
                <label>
                  <strong>Status:</strong>
                </label>
                {currentMybook.published ? "Published" : "Pending"}
              </div>
            </form>

            {currentMybook.published ? (
              <button
                className="badge badge-primary mr-2"
                onClick={() => this.updatePublished(false)}
              >
                UnPublish
              </button>
            ) : (
              <button
                className="badge badge-primary mr-2"
                onClick={() => this.updatePublished(true)}
              >
                Publish
              </button>
            )}

            <button
              className="badge badge-danger mr-2"
              onClick={this.deleteMybook}
            >
              Delete
            </button>

            <button
              type="submit"
              className="badge badge-success"
              onClick={this.updateMybook}
            >
              Update
            </button>
            <p>{this.state.message}</p>
          </div>
        ) : (
          <div>
            <br />
            <p>Please click on a Mybook...</p>
          </div>
        )}
      </div>
    );
  }
}