import React, { Component } from "react";
import MybookDataService from "../services/mybook.service";
import { Link } from "react-router-dom";

export default class MybooksList extends Component {
  constructor(props) {
    super(props);
    this.onChangeSearchTitle = this.onChangeSearchTitle.bind(this);
    this.retrieveMybooks = this.retrieveMybooks.bind(this);
    this.refreshList = this.refreshList.bind(this);
    this.setActiveMybook = this.setActiveMybook.bind(this);
    this.removeAllMybooks = this.removeAllMybooks.bind(this);
    this.searchTitle = this.searchTitle.bind(this);

    this.state = {
      mybooks: [],
      currentMybook: null,
      currentIndex: -1,
      searchTitle: ""
    };
  }

  componentDidMount() {
    this.retrieveMybooks();
  }

  onChangeSearchTitle(e) {
    const searchTitle = e.target.value;

    this.setState({
      searchTitle: searchTitle
    });
  }

  retrieveMybooks() {
    MybookDataService.getAll()
      .then(response => {
        this.setState({
          mybooks: response.data
        });
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  refreshList() {
    this.retrieveMybooks();
    this.setState({
      currentMybook: null,
      currentIndex: -1
    });
  }

  setActiveMybook(mybook, index) {
    this.setState({
      currentMybook: mybook,
      currentIndex: index
    });
  }

  removeAllMybooks() {
    MybookDataService.deleteAll()
      .then(response => {
        console.log(response.data);
        this.refreshList();
      })
      .catch(e => {
        console.log(e);
      });
  }

  searchTitle() {
    MybookDataService.findByTitle(this.state.searchTitle)
      .then(response => {
        this.setState({
          mybooks: response.data
        });
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
  }

  render() {
    // ...
    const { searchTitle, mybooks, currentMybook, currentIndex } = this.state;

    return (
      <div className="list row">
        <div className="col-md-8">
          <div className="input-group mb-3">
            <input
              type="text"
              className="form-control"
              placeholder="Search by title"
              value={searchTitle}
              onChange={this.onChangeSearchTitle}
            />
            <div className="input-group-append">
              <button
                className="btn btn-outline-secondary"
                type="button"
                onClick={this.searchTitle}
              >
                Search
              </button>
            </div>
          </div>
        </div>
        <div className="col-md-6">
          <h4>Mybooks List</h4>

          <ul className="list-group">
            {mybooks &&
              mybooks.map((mybook, index) => (
                <li
                  className={
                    "list-group-item " +
                    (index === currentIndex ? "active" : "")
                  }
                  onClick={() => this.setActiveMybook(mybook, index)}
                  key={index}
                >
                  {mybook.title}
                </li>
              ))}
          </ul>

          <button
            className="m-3 btn btn-sm btn-danger"
            onClick={this.removeAllMybooks}
          >
            Remove All
          </button>
        </div>
        <div className="col-md-6">
          {currentMybook ? (
            <div>
              <h4>Mybook</h4>
              <div>
                <label>
                  <strong>Title:</strong>
                </label>{" "}
                {currentMybook.title}
              </div>
              <div>
                <label>
                  <strong>Description:</strong>
                </label>{" "}
                {currentMybook.description}
              </div>
              <div>
                <label>
                  <strong>Status:</strong>
                </label>{" "}
                {currentMybook.published ? "Published" : "Pending"}
              </div>

              <Link
                to={"/mybooks/" + currentMybook.id}
                className="badge badge-warning"
              >
                Edit
              </Link>
            </div>
          ) : (
            <div>
              <br />
              <p>Please click on a Mybook...</p>
            </div>
          )}
        </div>
      </div>
    );
  }
}