import React, { Component } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";

import AddMybook from "./components/add-mybook.component";
import Mybook from "./components/mybook.component";
import MybooksList from "./components/mybooks-list.component";

class App extends Component {
  render() {
    return (
      <Router>
        <nav className="navbar navbar-expand navbar-dark bg-dark">
          <Link to={"/mybooks"} className="navbar-brand">
            bezKoder
          </Link>
          <div className="navbar-nav mr-auto">
            <li className="nav-item">
              <Link to={"/mybooks"} className="nav-link">
                Mybooks
              </Link>
            </li>
            <li className="nav-item">
              <Link to={"/add"} className="nav-link">
                Add
              </Link>
            </li>
          </div>
        </nav>

        <div className="container mt-3">
          <Switch>
            <Route exact path={["/", "/mybooks"]} component={MybooksList} />
            <Route exact path="/add" component={AddMybook} />
            <Route path="/mybooks/:id" component={Mybook} />
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;