import http from "../http-common";

class MybookDataService {
  getAll() {
    return http.get("/mybooks");
  }

  get(id) {
    return http.get(`/mybooks/${id}`);
  }

  create(data) {
    return http.post("/mybooks", data);
  }

  update(id, data) {
    return http.put(`/mybooks/${id}`, data);
  }

  delete(id) {
    return http.delete(`/mybooks/${id}`);
  }

  deleteAll() {
    return http.delete(`/mybooks`);
  }

  findByTitle(title) {
    return http.get(`/mybooks?title=${title}`);
  }
}

export default new MybookDataService();